.feed{
    flex: 0.4;
    border-right: 1px solid var(--twitter-background);
    min-width: fit-content;
    overflow-y: scroll;
}



.feedHeader{
    position: sticky;
    top: 0;
    background-color: white;
    z-index: 100;
    border: 1px solid var(--twitter-background);
    padding: 15px 20px;
    border-bottom: 5px solid var(--twitter-background);
}

.feedHeader > h2{
    font-size: 20px;
    font-weight: 800;
}